import { useSearchParams, useParams } from 'next/navigation';
import { createSupabaseServer } from "@/lib/supabaseServer";

export default async function WeekPage() {
  const searchParams = useSearchParams();
  const params = useParams();

  const id = params.id;
  const week = params.week;

  const supabase = createSupabaseServer();

  const { data: replays } = await supabase
    .from("replays")
    .select("*")
    .eq("division", Number(id))
    .eq("week", Number(week));

  return (
    <div style={{ padding: 40 }}>
      <h1>Division {id} â€” Week {week}</h1>

      {!replays?.length && <p>No replays uploaded yet.</p>}

      {replays?.map(r => (
        <div key={r.id} style={{ marginBottom: 20 }}>
          <strong>{r.team1} vs {r.team2}</strong>
          <br />
          <a href={r.file_url} target="_blank">View Replay</a>
        </div>
      ))}
    </div>
  );
}